<template>
	<div class="box">
		<header class="bar bar-nav">
			<div class="searchbar">
				<a class="searchbar-cancel">取消</a>
				<div class="search-input">
					<label class="icon icon-search" for="search"></label>
					<input type="search" id='search' placeholder='宝宝感冒了怎么办...' />
				</div>
			</div>
		</header>
		<nav class="bar bar-tab">
			<a class="tab-item active" href="#">
				<span class="icon icon-home"></span>
				<span class="tab-label">首页</span>
			</a>
			<a class="tab-item" href="grow.html" external>
				<span class="icon icon-camera"></span>
				<span class="tab-label">成长</span>
			</a>
			<a class="tab-item" href="diary.html" external>
				<span class="icon icon-paperfile"></span>
				<span class="tab-label">日记</span>
			</a>
			<a class="tab-item" href="#">
				<span class="icon icon-profile"></span>
				<span class="tab-label">我</span>
			</a>
		</nav>

		<div class="content">
			<!-- Slider -->
			<div class="swiper-container" data-space-between='0' data-autoplay='2000'>
				<div class="swiper-wrapper">
					<div class="swiper-slide"><img src="../assets/banner.jpg" alt=""></div>
					<div class="swiper-slide"><img src="../assets/banner2.jpg" alt=""></div>
					<div class="swiper-slide"><img src="../assets/banner3.jpg" alt=""></div>
				</div>
				<div class="swiper-pagination"></div>
			</div>

			<div class="content-padded grid-demo icon-link">
				<div class="row">
					<div class="col-25">
						<div><img src="../assets/icon1.png"></div>
						<p>营养膳食</p>
					</div>
					<div class="col-25">
						<div><img src="../assets/icon2.png"></div>
						<p>智力开发</p>
					</div>
					<div class="col-25">
						<div><img src="../assets/icon3.png"></div>
						<p>品德教育</p>
					</div>
					<div class="col-25">
						<div><img src="../assets/icon4.png"></div>
						<p>习惯培养</p>
					</div>
				</div>
				<div class="row">
					<div class="col-25">
						<div><img src="../assets/icon5.png"></div>
						<p>性格养成</p>
					</div>
					<div class="col-25">
						<div><img src="../assets/icon6.png"></div>
						<p>亲子互动</p>
					</div>
					<div class="col-25">
						<div><img src="../assets/icon7.png"></div>
						<p>专家在线</p>
					</div>
					<div class="col-25">
						<div><img src="../assets/icon8.png"></div>
						<p>儿科疾病</p>
					</div>
				</div>
			</div>

			<div class="content-padded  msg-list">
				<h3 class="msg-title">消息推送</h3>
				<div class="row msg-box" v-for="item in list">
					<div class="col-40">
						<img v-bind:src="item.pic" v-bind:data-id="item.id"  alt="" v-on:click="del($event)" />
					</div>
					<div class="col-60">
						<h4>{{ item.title }}</h4>
						<p>{{ item.disc }}</p>
					</div>
				</div>
				<p>
					<a href="#" class="button button-big" v-on:click="loadmore">加载更多</a>
				</p>
			</div>

		</div>

	</div>
</template>

<script>
	export default {
		name: 'carindex',
		data() {
			return {
				id: '',
				name: '',
				list: [{
						"id": 0,
						"title": "根据传入的id获取到这个要删除数据",
						"pic": require('../assets/pic1.jpg'),
						"disc": "调用list.findIndex()方法，根据传入的id获取到这个要删除数据的索引值"

					}
				]
			}
		},
		methods: {
			del: function(element) {
				
				if(!confirm("是否删除数据？")) {
					return;
				}
				//调用list.findIndex()方法，根据传入的id获取到这个要删除数据的索引值
//				var index = this.list.findIndex(function(item) {
//					return item.id == id;
//				});
				
				//调用list.splice(删除的索引，删除的元素个数)
				this.list.splice(1,element.currentTarget.getAttribute("data-id"),);
			},
			add: function() {
				//包装成list要求的对象
				var tem = {
					id: this.id,
					name: this.name,
					time: new Date()
				};
				//将tem追加到list数组中
				this.list.push(tem);
				//清空页面上的文本框中的数据
				this.id = "";
				this.name = "";
			},
			loadmore: function() {
				this.$http.get("mock/newsList").then(res => {
						this.list = this.list.concat(res.data.list);
						//						this.list.push(res.data.list)

					})
					.catch(err => {
						console.log(err);
					});
			}
		}
	}
</script>
<style scoped>
	header.bar {
		background-color: #9bcb67;
	}
	
	.content {
		background-color: #fff;
	}
	
	.swiper-pagination-bullet-active {
		background-color: #9bcb67;
	}
	
	.swiper-wrapper>.swiper-slide>img {
		width: 100%;
	}
	
	.msg-list img {
		width: 100%;
	}
	
	.icon-link .col-25 {
		text-align: center;
	}
	
	.icon-link img {
		width: 80%;
	}
	
	.icon-link p {
		line-height: .1rem;
		font-size: .7rem;
		color: #555;
	}
	
	.msg-title {
		font-size: .8rem;
		text-align: left;
		font-weight: 400;
		background: url("../assets/msg-title-icon.gif") no-repeat;
		text-indent: 1rem;
		background-size: .8rem;
		line-height: .8rem
	}
	
	.msg-box {
		border-bottom: 1px solid #efefef;
		padding: .5rem 0;
	}
	
	.msg-box>.col-60>h4 {
		text-align: left;
		margin: 0;
		padding: 0;
		font-weight: 400;
		font-size: .8rem;
		color: #000;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
	}
	
	.msg-box>.col-40>img {
		float: left;
		border: 1px solid #efefef;
	}
	
	.msg-box>.col-60>p {
		text-align: left;
		margin: .5rem 0 0 0;
		padding: 0;
		font-weight: 400;
		font-size: .7rem;
		color: #555;
	}
	
	.bar {
		background-color: #fff;
		border-top: 1px solid #c5c5c5;
	}
	/*svg sprite*/
	
	.bar-tab .tab-item.active,
	.bar-tab .tab-item:active {
		color: #9bcb67
	}
	
	.bar-tab svg {
		width: 1rem;
		height: 1rem;
		fill: #999;
	}
	
	.bar-tab>.active svg {
		fill: #9bcb67;
	}
	/*svg sprite*/
</style>


