import Mock from "mockjs";
Mock.mock("mock/newsList", {
  "list|5": [
    //随机返回5项
    {
    	id:"@increment()",
      pic: "@image('200x150', '#50B347', '#FFF', 'Mock.js')",    
      title:"@csentence(11, 15)",
      disc:"@csentence"   
    }
  ]
});



